html {
    min-height: 100%;
    position: relative;
}

body {
    margin-bottom: 60px;
}

.video-bg {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    min-width: 100%;
    min-height: 100%;
    height: auto;
    width: auto;
    z-index: 1;
}

.video-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: rgba(0, 0, 0, .7);
    display: flex;
    align-items: center;
    justify-content: center;

    .jumbotron {
        text-align: center;
        color: white;
        background: none;

        h1 {
            font-weight: bold;
        }

        a {
            color: white;
            font-size: 18px;
            margin: 0 10px;
        }
    }
}

.footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 60px;
    background-color: #f5f5f5;
    line-height: 60px;
    z-index: 3;
}

.albums {

}

.album-cover {
    width: 100%;
    height: auto;        
}